.App {
  text-align: left;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.header-text {
  text-align: center;
}

.form-input {
  width: 400px;
}

.text-input {
  width: 100%;
}

.metadata-container {
  width: 400px;
}

.send-button {
  background-color: rgb(224, 84, 84);
  border: white solid 1px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 25px;
  height: 40px;
  width: 200px;
}
